.card.card-content {
  box-shadow: 0 3px 5px 0 $card-content-border-color !important;
  border: none !important;
  border-radius: 0 !important;

  .card-header {
    padding: 20px 25px !important;
    border-bottom: 1px solid $card-content-border-color !important;

    .card-title {
      font-size: 20px !important;
      font-weight: 400 !important;
    }

    .circle {
      width: 50px;
      height: 50px;
      padding: 10px 16px;
      border-radius: 25px;
      font-size: 18px;
      line-height: 1.33;
      display: inline-block;
      margin-right: 10px;
      color: $gray-400;
      background-color: transparent;
      border: 1px solid $gray-400;
      > i.fa {
        margin-top: 5px;
      }
    }
  }

  .card-body {
    padding: 25px;
    border-top: 1px solid $card-content-border-color;
    border-bottom: 1px solid $card-content-border-color;
  }
}
